<template>
    <el-table
            ref="elTable"
            @cell-dblclick="rowDoubleClick"
            @row-click="rowClick"
            @selection-change="$emit('selection-change', $event)"
            class="base-table full-width"
            :data="data"
            :border="border"
            :stripe="stripe"
            :default-sort="defaultSort"
            :height="height"
            :show-summary="showSummary"
            :tree-props="treeProps"
            row-key="id"
            highlight-current-row
    >
        <el-table-column
                v-if="selection"
                width="50"
                type="selection"
        ></el-table-column>
        <el-table-column
                v-if="order"
                align="center"
                label=""
                width="60"
                type="index"
        ></el-table-column>
        <el-table-column
                v-for="item in columns"
                :key="item.prop"
                :prop="item.prop"
                :label="item.label"
                :width="item.width"
                :fixed="item.fixed"
                :sortable="item.sortable"
                show-overflow-tooltip
                :align="item.align"
        >
            <template slot-scope="{ row }">
                <template v-if="item.clickEnable">
                    <el-button @click="$emit('click', row, item)" type="text">
                        {{ formatData(item, row) }}
                    </el-button>
                </template>
                <template v-else-if="item.type === 'dic'">
                    <dictionary-span
                            :type="item.dicType"
                            :binddata="row[item.prop]"
                    ></dictionary-span>
                </template>
                <template v-else-if="item.render">
                    <expand-dom
                            :column="item"
                            :row="row"
                            :render="item.render"
                    ></expand-dom>
                </template>
                <template v-else>
                    {{ formatData(item, row) }}
                </template>
            </template>
        </el-table-column>
        <slot></slot>
    </el-table>
</template>

<script>
	import {format} from 'date-fns';

	/**
	 * 
	 *
	 * @prop {Array} data example[{ [prop]: String }]
	 * @prop {Array} columns 
	 * - example[{
     *     prop: String, label: String, width: Number, sortable: Boolean,
     *     clickEnable: Boolean, type: String, dateFormat: String, formatter: Function
     *   }]
	 * - sortable 
	 * - clickEnable  click  click 
	 * - type date  dic
	 * - dateFormat   'yyyy-MM-dd HH:mm:ss'
	 * - formatter(value) 
	 *
	 * @prop {Boolean} border 
	 * @prop {Boolean} stripe 
	 * @prop {String} size  mini
	 * @prop {Boolean} order 
	 * @prop {Object} defaultSort example{ prop: 'area', order: 'descending' }
	 * @prop {String} height 
	 * @prop {Boolean} showSummary  false
	 * @prop {Boolean} selection  false
	 * @prop {Object} treeProps  props  { children: 'children' }
	 *
	 * @event selection-change 
	 * @event click(data, column) data column 
	 * @event row-click(row, column, cell, event 
	 *
	 * @method setCurrentRow(row) 
	 */
	export default {
		name: 'BlackElTable',
		props: {
			data: Array,
			columns: Array,
			border: {
				type: Boolean,
				default: false
			},
			stripe: {
				type: Boolean,
				default: true
			},
			size: {
				type: String,
				default: 'small'
			},
			defaultSort: Object,
			height: {
				type: String,
				default: 'auto'
			},
			showSummary: {
				type: Boolean,
				default: false
			},
			order: {
				type: Boolean,
				default: true
			},
			selection: {
				type: Boolean,
				default: false
			},
			treeProps: {
				type: Object,
				default () {
					return {children: 'children'};
				}
			}
		},
		components: {
			expandDom: {
				functional: false,
				props: {
					row: Object,
					render: Function,
					index: Number,
					column: {
						type: Object,
						default: null
					}
				},
				render: function (h) {
					return this.render(h, this.row);
				}
			}
		},
		methods: {
			// 
			formatData (column, row) {
				const value = row[column.prop];
				if (value) {
					if (column.type === 'date') {
						return format(value, column.dateFormat || 'yyyy-MM-dd HH:mm:ss');
					} else if (column.formatter instanceof Function) {
						return column.formatter(value, row);
					} else {
						return value;
					}
				} else {
					return value;
				}
			},

			// 
			rowDoubleClick (row, column, cell, event) {
				this.$emit('cell-dblclick', row, column, cell, event);
			},

			// 
			rowClick (row, column, cell, event) {
				this.$emit('row-click', row, column, cell, event);
			},

			// 
			setCurrentRow (row) {
				this.$refs.elTable.setCurrentRow(row);
			},

			toggleSelection (row) {
				this.$refs.elTable.toggleRowSelection(row);
			},
			// 
			clearSelection () {
				this.$refs.elTable.clearSelection();
			}
		}
	};
</script>

